* {
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: Montserrat, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.fjalla {
	font-family: Fjalla One, Arial, Helvetica, sans-serif;
}

:root {
	--dark_blue: #0554a5;
	--light_blue: #40c4eb;
	--shadow_grey: #d6d6d6;
}

.styledInput {
	font-size: 14px;
	background-color: color-mix(in srgb, var(--shadow_grey) 20%, #fff 80%);
	border: none;
	border-bottom: 1px solid black;
	border-top-right-radius: 4px;
	border-top-left-radius: 4px;
	padding-left: 4px;
	padding-top: 4px;
}
.styledInput:focus {
	outline: none;
	border-bottom: 1px solid var(--light_blue);
}

input[type="submit"],
.styledButton {
	text-transform: capitalize;
	border: none;
	border-radius: 5px;
	padding: 8px 12px;
	background-color: var(--dark_blue);
	color: #fff;
	font-size: 14px;
	font-family: Montserrat;
	font-weight: 600;
}
input[type="submit"]:hover,
.styledButton:hover {
	background-color: color-mix(in srgb, var(--dark_blue) 90%, #fff 10%);
}
input[type="submit"]:active,
.styledButton:active {
	background-color: var(--dark_blue);
}

.wire {
	display: flex;
	justify-content: center;
	align-items: center;
	border: 2px solid black;
}

.navbar {
	height: 10vh;
}
.content {
	display: flex;
}
.leftContainer {
	flex-grow: 8;
}
.rightContainer {
	flex-grow: 2;
	height: 90vh;
}
.liveEvents {
	height: 30vh;
}
.allEvents {
	height: 60vh;
}
.footer {
	height: 50vh;
}

@media screen and (max-width: 900px) {
	.content {
		flex-direction: column;
	}
}
